
header {
    width: 100%;
    height: 140px;
    display: grid;
    grid-template-rows: 1fr 1fr;
  }
  
  
  
  header .header-icons-container {
    width: 100%;
    height: 50px;
    display: grid;
    background: #47cfac;
  
  }
  
  header .header-icons-container .icons {
    width: 280px;
    height: auto;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    justify-content: space-between;
    justify-self: end;
    padding-right: 20px;
  
  }
  
  header .icons span {
    color: white;
  }
  
  nav {
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 90px;
  
  }
  
  nav .nav-logo-container {
    padding-left: 50px;
  
  }
  
  nav .profile-link {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 50px;
  
  }
  
  nav .nav-logo-container img{
    width: 220px;
    margin-top: 10px;
  }
  
  nav .profile-link a {
    color: black;
    border-bottom: 1px solid black;
  }



@media screen and (max-width: 675px) {
  .icons a {
    font-size: 5px;
  }
  
  nav .nav-logo-container {
    padding-left: 20px;
  
  }

  nav .profile-link {
    padding-right: 20px;
  
  }
}

