.home-body {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  
  .home-main {
    display: grid;
    grid-template-columns: 1fr 4fr 1fr;
    height: 100%;
    background-image: url(/platzi-blog/static/media/Cover.fed2338e.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  
  .home-main section {
    display: grid;
    grid-column: 2;
    justify-items: center;
    height: 350px;
    margin-top: 80px;
  }
  
  .home-main-text {
    font-size: 30px;
    font-weight: 700px;
    letter-spacing: 10px;
    color: white;
    text-align: center;
  }
  
  
  .home-main-button {
    width: 110px;
    height: 50px;
    background-color: #48cfad;
    display: grid;
    text-align: center;
    font-weight: 700px;
    font-size: 18px;
    line-height: 50px;
  }
  

.blogs-main {
    display: grid;
  
  }
  .blogs-news-container {
    background-color: #e6e9ed;
    padding: 0 50px 40px;
  }
  
  .blogs-main-new {
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-template-rows: 2fr;
  }
  
  .blogs-news-img-container {
    grid-column: 1;
  }
  
  .blogs-news-img-container img {
    width: 85%;
  }
  
  .blogs-news-info-container {
    grid-column: 2;
  }
  
  .blogs-news-info-container p {
    margin-bottom: 35px;
  }
  
  .blogs-post-container {
    padding: 0 50px 40px;
  
  }
  
  .blogs-post-container h3 {
    border-bottom: 1px solid #cdd2da;
    padding-bottom: 20px;
    text-align: center;
  }
  
  .blogs-button {
    border: 1px solid #47cfac;
    padding: 10px 15px;
    font-size: 12px;
  }
  
  .blogs-post-container .post-container {
    display: inline-block;
    padding-left: 10px;
    max-width: 30%;
    margin-bottom: 50px;
  }
  
  .blogs-post-container .post-container p {
    margin-bottom: 35px;
  }
  
  
  .blogs-post-container .post-container img {
    width: 100%;
  
  }
  


  @media (max-width: 900px) {
    .blogs-main-new {
      grid-template-columns: 1fr;
    }
  
  
    .blogs-news-info-container {
      grid-column: 1;
    }
  
    .blogs-post-container .post-container {
      max-width: 45%;
    }
  
  
  }
  
  @media (max-width: 600px) {
    .blogs-post-container .post-container {
      max-width: 95%;
    }
  
  }
  

.blogpost-img-container {
    padding: 0 50px 40px;
  }
  
  .blogpost-img-container img {
    width: 100%;
  }
  
  .blogpost-main-container {
    padding: 0 50px 40px;
  }
  
  .blogpost-main-container h3 {
    border-bottom: 1px solid #cdd2da;
    padding-bottom: 20px;
  }
  
  .blogpost-main-container section h1 {
    font-style: 35px;
  }
  


.contact-main-container {
    width: 100%;
    min-height: 250px;
    background-color: #e6e9ed;
    text-align: center;
    padding-bottom: 50px;
  }
  
  .contact-main-container div {
    display: inline-block;
    width: 48%;
    height: 100%
  }
  
  
  .contact-main-container div img {
    width: 100px;
  }
  
  .contact-right, .contact-left {
    text-align: left;
    text-align: initial;
    margin-top: 85px;
  }
  
  .contact-main-container div a {
    border-bottom: 1px solid black;
    padding-bottom: 5px;
  }
  
  .profile-main-container{
    background-color: #e6e9ed;
    padding: 70px 40px;
    margin-bottom: 50px;
  }
  
  .profile-main-container .profile-container{
    display: flex;
  }
  
  .profile-container img{
    
    width: 300px;
    margin-right: 30px;
  }
  
  .project-container p {
    padding: 0 5px;
  }
  .project-container h4 {
    padding: 0 5px;
  }
  
  .projects-main-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
  
  .profile-main-projects h3 {
    text-align: center;
    border-bottom: 1px solid #cdd2da;
    padding-bottom: 35px;
  }
  
  
  .profile-main-projects {
   padding: 0 50px 40px;
  }
  

@media screen and (max-width: 675px) {
    .profile-container {
        flex-direction: column;
    }

    .profile-container img{
      
      max-width: 80%;
      margin: 0 auto
    }

    .projects-main-container {
        grid-template-columns: 1fr;
      }
      
      .contact-main-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 20px 0px;

    }
    
    .contact-right, .contact-left {
      margin-top: 0;
    }

    .contact-main-container div {
      width: 95%;
    }
}

footer {
    width: 100%;
    height: 80px;
    background-color: #22272d;
    border-top: 35px solid #48cfac;
    text-align: center;
    margin: 0 auto;
  }
  
  footer p{
    color: white;
    margin-top: 30px;
  
  }
  

header {
    width: 100%;
    height: 140px;
    display: grid;
    grid-template-rows: 1fr 1fr;
  }
  
  
  
  header .header-icons-container {
    width: 100%;
    height: 50px;
    display: grid;
    background: #47cfac;
  
  }
  
  header .header-icons-container .icons {
    width: 280px;
    height: auto;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    justify-content: space-between;
    justify-self: end;
    padding-right: 20px;
  
  }
  
  header .icons span {
    color: white;
  }
  
  nav {
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 90px;
  
  }
  
  nav .nav-logo-container {
    padding-left: 50px;
  
  }
  
  nav .profile-link {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 50px;
  
  }
  
  nav .nav-logo-container img{
    width: 220px;
    margin-top: 10px;
  }
  
  nav .profile-link a {
    color: black;
    border-bottom: 1px solid black;
  }



@media screen and (max-width: 675px) {
  .icons a {
    font-size: 5px;
  }
  
  nav .nav-logo-container {
    padding-left: 20px;
  
  }

  nav .profile-link {
    padding-right: 20px;
  
  }
}

